import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import {
  CIcon
} from '@coreui/icons-react';

import { withRouter } from "react-router";

class Login extends Component {
  constructor(prop) {
    super(prop)
    this.state = {
      email: "",
      password: "",
    }
  }

  componentDidMount=()=>{
    if(localStorage.getItem("accountLoginFlag") == "yes"){
      this.props.history.push("/apps/programs/plan");
    }
  }
  onEmailChange = (e) => {
    this.setState({ email: e.target.value })
    // console.log("sdf" , e.target.value)
  }
  onPasswordChange = (e) => {
    this.setState({ password: e.target.value })
  }

  onLogin = () => {
    // console.log("sdf")
    if (this.state.email == "admin" && this.state.password == "admin1") {
      alert("Success !")
      localStorage.setItem("accountLoginFlag", "yes");
      this.props.history.push("/apps/programs/plan");
    } else {
      alert("email or password is incorrect !")
    }
  }
  render() {
    return (
      <div className="c-app c-default-layout flex-row align-items-center">
        <CContainer>
          <CRow className="justify-content-center">
            <CCol md="8">
              <CCardGroup>
                <CCard className="p-4">
                  <CCardBody>
                    <CForm>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <CInputGroup className="mb-3">
                        <CInputGroupPrepend>
                          <CInputGroupText>
                            <CIcon name="cil-user" />
                          </CInputGroupText>
                        </CInputGroupPrepend>
                        <CInput type="text" placeholder="Username" autoComplete="username" onChange={this.onEmailChange} />
                      </CInputGroup>
                      <CInputGroup className="mb-4">
                        <CInputGroupPrepend>
                          <CInputGroupText>
                            <CIcon name="cil-lock-locked" />
                          </CInputGroupText>
                        </CInputGroupPrepend>
                        <CInput type="password" placeholder="Password" autoComplete="current-password" onChange={this.onPasswordChange} />
                      </CInputGroup>
                      <CRow>
                        <CCol xs="6">
                          {/* <button color="primary" className="px-4" onClick={this.onLogin}>Login</button> */}
                          <CButton color="primary" className="px-4" onClick={this.onLogin}>Login</CButton>
                        </CCol>
                        <CCol xs="6" className="text-right">
                          <CButton color="link" className="px-0">Forgot password?</CButton>
                        </CCol>
                      </CRow>
                    </CForm>
                  </CCardBody>
                </CCard>
                <CCard className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CCardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                      <Link to="/register">
                        <CButton color="primary" className="mt-3" active tabIndex={-1}>Register Now!</CButton>
                      </Link>
                    </div>
                  </CCardBody>
                </CCard>
              </CCardGroup>
            </CCol>
          </CRow>
        </CContainer>
      </div>
    );
  }
}

// export default Login;
export default withRouter(Login);
